import React from 'react';
import Header from './Header';
import Home from './Home';
import Navbar from './Navbar';
import Activity from './Activity';
import Explore from './Explore';
import Profile from './Profile';
import NewPost from './NewPost';
import initialStore from '../utils/initialStore'
import css from './App.module.css';

class App extends React.Component{
	
	constructor(props){
		super(props);
		this.state = {
			page:"home",
			store: initialStore
		}
		this.setPage = this.setPage.bind(this)
	}

	addLike(postId){
		const like = {
			userId: this.state.store.currentUserId, 
			postId, // make sure you understand this shorthand syntax
			datetime: new Date().toISOString()
		};
		
		this.setState(state=>({
			store:{
			  ...state.store,// spread props. make sure you understand this
			  likes: state.store.likes.concat(like)
			}
		}));
	  }

	  removeLike(postId){
		let userId=this.state.store.currentUserId;
		this.setState(state=>({
			store:{
				...state.store,// spread props. make sure you understand this
				likes: state.store.likes.filter((element) => element.userId!==userId && element.postId!==postId)
			  }
		  }));
	  }
	
	setPage(page){
		this.setState({ page: page});
	}

	renderMain(page){
		switch(page){
		case "home": return <Home store={this.state.store}/>;
		case "activity": return <Activity/>;
		case "explore": return <Explore/>;
		case "profile": return <Profile/>;
		case "newpost": return <NewPost/>;
		default: return <Home/>;
		}
	}

	render(){
		return(
			<div className={css.container}>
				<Header/>
				<main className={css.content}>
				{this.renderMain(this.state.page)}
			</main>
			<Navbar onNavChange={this.setPage}/>
			</div>
		);
	}
}

export default App;
